/* Fondo general */
body {
  font-family: Arial, sans-serif;
  background:#073058;
  color: #2C3E50;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
  max-width: 100%;
  max-height: 00vh;
  
}

/* Contenedor principal */
.container {
  background: #F9FAFB;
  color: #2C3E50;
  box-shadow: 0 8px 100px rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  padding: 10px;
  max-width: 1000px;
  margin: 10px auto;
  
}

/* Pestañas */
.tabs {
  display: flex;
  border-bottom: 2px solid #B0BEC5;
  margin-bottom: 20px;
}

.tab {
  flex: 1;
  padding: 10px 15px;
  text-align: center;
  cursor: pointer;
  background: #E3EDF7;
  border: 1px solid #CFD8DC;
  border-bottom: none;
  border-radius: 12px 12px 0 0;
  color: #1E3A5F;
  font-weight: bold;
  transition: background-color 0.3s, color 0.3s;
}

.tab.active {
  background: #455A64;
  color: #FFFFFF;
}

.tab:hover {
  background: #607D8B;
  color: #FFFFFF;
}

/* Contenido de Pestañas */
.tab-content {
  padding: 10px;
}

/* Los Botones */
.button-group {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-bottom: 10px;
}

.button-group button {
  background-color: #2C7D90;
  color: #FFFFFF;
  border: none;
  padding: 8px 12px;
  border-radius: 5px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.button-group button:hover {
  background-color: #4095A8;
  transform: scale(1.05);
}

/* Los Formularios */
label {
  font-weight: bold;
  display: block;
  margin-bottom: 0px;
}

textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #B0BEC5;
  border-radius: 6px;
  background: #F5F7FA;
  transition: background-color 0.3s, box-shadow 0.2s;
}

textarea:focus {
  background-color: #E3EDF7;
  border-color: #2C7D90;
  box-shadow: 0 0 5px rgba(44, 125, 144, 0.5);
  outline: none;
}

textarea, input {
  font-family: 'Roboto', Arial, sans-serif;
  font-weight: 700;
}

/* El Grid */
.grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px 40px;
}

/* Responsividad */
@media (max-width: 768px) {
  .grid {
    grid-template-columns: repeat(1, 1fr);
  }
}

.antecedentes-horizontal {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.checkbox-inline {
  margin-right: 15px;
  display: flex;
  align-items: center;
}

.styled-input {
  padding: 10px;
  border: 1px solid #CFD8DC;
  border-radius: 4px;
  width: 100%;
  box-sizing: border-box;
  font-size: 16px;
  margin-top: 5px;
  margin-bottom: 15px;
}

.grid-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px 20px;
}

.grid-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px 20px;
}

.grid-4 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px 20px;
}

.servicios-horizontal {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

/* Footer */
.footer {
  text-align: center;
  padding: 10px;
  background-color: #1E3A5F;
  color: #F9FAFB;
  border-radius: 0 0 12px 12px;
  margin-top: 20px;
  font-weight: bold;
}
/* Estilos para las áreas de texto con una sola línea */
.single-line-input {
  border: none;
  border-bottom: 1px solid #000;
  padding: 10px;
  width: 100%;
  box-sizing: border-box;
  font-size: 16px;
  margin-bottom: 15px; /* Solo margen inferior */
  resize: none;
}

/* Estilos para las áreas de texto con una sola línea */
.single-line-input {
  border: none;
  border-bottom: 1px solid #000;
  padding: 10px;
  width: 100%;
  box-sizing: border-box;
  font-size: 16px;
  margin-bottom: 15px; /* Solo margen inferior */
  resize: none;
}

/* Estilos para las áreas de texto que se adaptan al contenido */
.auto-resize-textarea {
  border: 1px solid #d1d5db;
  border-radius: 6px;
  padding: 10px;
  width: 100%;
  box-sizing: border-box;
  font-size: 16px;
  margin-bottom: 15px; /* Solo margen inferior */
  resize: vertical;
  overflow: hidden;
}

/* Estilo específico para el textarea de Diagnóstico */
.diagnostico-textarea {
  min-height: 5em; /* Altura mínima de 5 líneas */
  max-height: 20em; /* Altura máxima de 10 líneas */
}

/* Estilo específico para el textarea de Tratamientos */
.tratamientos-textarea {
  min-height: 7em; /* Altura mínima de 5 líneas */
  max-height: 20em; /* Altura máxima de 10 líneas */
}

/* Estilo específico para el textarea de Medicamentos */
.medicamentos-textarea {
  min-height: 30em; /* Altura mínima de 5 líneas */
  max-height: 10em; /* Altura máxima de 10 líneas */
}

/* Estilo específico para el textarea de Observaciones */
.observaciones-textarea {
  min-height: 5em; /* Altura mínima de 5 líneas */
  max-height: 10em; /* Altura máxima de 10 líneas */
}

/* Diseño de la Grid para 3 columnas */
.grid-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px 20px;
}

/* Estilo para que el textarea ocupe todo el ancho */
.full-width {
  grid-column: span 3;
}
/* Estilos para impresión */
@media print {
  @page {
    size: A4; /* Tamaño de la página A4 */
    margin: 0; /* Quitar margen de encabezado y pie de página */
  }

  body {
    width: 21.59cm;
    height: 27.94cm;
    margin: 0;
    padding: 0;
  }

  .tabs, .footer {
    display: none;
  }

  .container {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 10px;
  }

  .tab-content {
    page-break-inside: avoid;
  }

  .button-group {
    display: none;
  }

  .single-line-input, .auto-resize-textarea {
    font-size: 12px;
    padding: 4px;
  }

  .form-group {
    margin-bottom: 5px; /* Reducir el margen inferior */
  }

  .grid-3 {
    gap: 5px 10px; /* Reducir el espacio entre columnas */
  }

  .full-width {
    grid-column: span 3;
  }
}
/* Estilos para agrupar checkboxes en 5 columnas */
.checkbox-group-5-columns {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
}
/* Estilos para agrupar en 2 columnas */
.grid-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
}
/* Estilos para agrupar checkboxes en 5 columnas */
.checkbox-group-2-columns {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
}
